#Gallery-Title {
  text-align: center;
  padding: 30px 0;
}
.hello {
  opacity: 1 !important;
}
.full {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
}
.full .content {
  background-color: rgba(0, 0, 0, 0.75) !important;
  height: 100%;
  width: 100%;
  display: grid;
}
.full .content img {
  left: 50%;
  transform: translate3d(0, 0, 0);
  animation: zoomin 1s ease;
  max-width: 100%;
  max-height: 100%;
  margin: auto;
}
.byebye {
  opacity: 0;
}
.byebye:hover {
  transform: scale(0.2) !important;
}
.gallery {
  display: grid;
  grid-column-gap: 8px;
  grid-row-gap: 8px;
  grid-template-columns: repeat(auto-fill, minmax(250px, 2fr));
  /* grid-auto-rows: 0px; */
}
.gallery img {
  max-width: 100%;
  border-radius: 8px;
  transition: all 1.5s ease;
  overflow: hidden;
}
.gallery .content {
  height: 100%;
}
.gallery .gallery-item {
  transition: grid-row-start 300ms linear;
  transition: transform 300ms ease;
  transition: all 0.5s ease;
  cursor: pointer;
  /* box-shadow: 0 0 6px #333; */
  /* position: relative; */
  overflow: hidden;
}
.gallery .gallery-item:hover {
  transform: scale(1.025);
}
.img-contant {
  background-color: hwb(198 43% 4% / 0.9);
  color: white;
  width: 100%;
  position: absolute;
  border-radius: 8px;
  height: 98%;
  /* z-index: 4564; */
  top: -100%;
  /* margin-left: 0.5%; */
  left: -100%;
  transition: all 0.5s;
  padding: 9px;
}
.content:hover .img-contant {
  left: 0;
  top: 0;
}
@media (max-width: 550px) {
  .gallery {
    grid-template-columns: repeat(auto-fill, minmax(30%, 1fr));
  }
}
@media (max-width: 450px) {
  .gallery {
    grid-template-columns: repeat(auto-fill, minmax(50%, 1fr));
  }
}
@-moz-keyframes zoomin {
  0% {
    max-width: 50%;
    transform: rotate(-30deg);
    filter: blur(4px);
  }
  30% {
    filter: blur(4px);
    transform: rotate(-80deg);
  }
  70% {
    max-width: 50%;
    transform: rotate(45deg);
  }
  100% {
    max-width: 100%;
    transform: rotate(0deg);
  }
}
